"use client";

import React, { useEffect } from "react";
// Import shadcn/ui Table components
import {
  Table,
  TableHeader,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
} from "@/components/ui/table";

// Custom hook to disable zooming
const useDisableZoom = () => {
  useEffect(() => {
    const preventKeyDown = (e) => {
      // Prevent zooming with Ctrl/Cmd + '+' or '-' or '='
      if (
        (e.ctrlKey || e.metaKey) &&
        (e.key === "+" || e.key === "-" || e.key === "=")
      ) {
        e.preventDefault();
      }
    };

    const preventWheel = (e) => {
      // Prevent zooming with Ctrl/Cmd + Mouse Wheel
      if (e.ctrlKey || e.metaKey) {
        e.preventDefault();
      }
    };

    window.addEventListener("keydown", preventKeyDown, false);
    window.addEventListener("wheel", preventWheel, { passive: false });

    // Clean up the event listeners on unmount
    return () => {
      window.removeEventListener("keydown", preventKeyDown, false);
      window.removeEventListener("wheel", preventWheel);
    };
  }, []);
};

const VaccineForm = ({ childData }) => {
  // Call the hook to disable zooming on this page/component
  useDisableZoom();

  return (
    <div className="p-32">
      {/* Header */}
      <h2 className="text-3xl font-bold text-left mb-6">
        Child Immunization Record
      </h2>

      {/* Child Information Table (Plain HTML, no borders) */}
      <table className="w-full mb-6">
        <tbody>
          <tr>
            <td className="px-4 py-2">
              <strong>Child’s Name:</strong> {childData.name}
            </td>
            <td className="px-4 py-2">
              <strong>Mother’s Name:</strong> {childData.motherName}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Date of Birth:</strong> {childData.dob}
            </td>
            <td className="px-4 py-2">
              <strong>Father’s Name:</strong> {childData.fatherName}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Place of Birth:</strong> {childData.placeOfBirth}
            </td>
            <td className="px-4 py-2">
              <strong>Birth Height:</strong> {childData.birthHeight}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Address:</strong> {childData.address}
            </td>
            <td className="px-4 py-2">
              <strong>Birth Weight:</strong> {childData.birthWeight}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Health Center:</strong> {childData.healthCenter}
            </td>
            <td className="px-4 py-2">
              <strong>Sex:</strong> {childData.sex}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Barangay:</strong> {childData.barangay}
            </td>
            <td className="px-4 py-2">
              <strong>Family No.:</strong> {childData.familyNumber}
            </td>
          </tr>
        </tbody>
      </table>

      {/* Immunization Table wrapped in a div with the design */}
      <div
        className="w-full p-5 relative"
        style={{
          backgroundImage: "url('/bg_image_form.png')",
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center",
          backgroundSize: "cover",
        }}
      >
        {/* eslint-disable-next-line @next/next/no-img-element */}
        <img
          src="/icon 1.png"
          alt="Icon"
          className="absolute top-[190px] right-[55px] w-44 h-44"
        />
        {/* eslint-disable-next-line @next/next/no-img-element */}
        <img
          src="/icon 2.png"
          alt="Icon"
          className="absolute top-[50px] right-[90px] w-28 h-28"
        />
        {/* eslint-disable-next-line @next/next/no-img-element */}
        <img
          src="/icon 3.png"
          alt="Icon"
          className="absolute top-[365px] right-[0px] w-64 h-32"
        />
        {/* Add border-separate and border-spacing classes here to simulate margin between rows */}
        <Table className="w-[85%] h-full border-separate border-spacing-x-4 border-spacing-y-4">
          <TableHeader>
            <TableRow>
              <TableHead className="border border-black bg-[#ffb43d] text-black p-2 text-center font-extrabold">
                Bakuna
              </TableHead>
              <TableHead className="border border-black bg-[#ffb43d] text-black p-2 text-center font-extrabold">
                Doses
              </TableHead>
              <TableHead className="border border-black bg-[#ffb43d] text-black p-2 text-center font-extrabold">
                Petsa ng Bakuna (MM/DD/YY)
              </TableHead>
              <TableHead className="border border-black bg-[#ffb43d] text-black p-2 text-center font-extrabold">
                Remarks
              </TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {childData.vaccinations.map((vaccine, index) => (
              <TableRow key={index} className="bg-white">
                <TableCell className="border border-black p-2 text-center">
                  {vaccine.name}
                </TableCell>
                <TableCell className="border border-black p-2 text-center">
                  {vaccine.doses}
                </TableCell>
                <TableCell className="p-2 text-center">
                  <div className="flex justify-center gap-2">
                    {Array.from({ length: vaccine.doses }).map(
                      (_, doseIndex) => (
                        <span
                          key={doseIndex}
                          className="flex-1 border border-black p-1"
                        >
                          {vaccine.date}
                        </span>
                      )
                    )}
                  </div>
                </TableCell>
                <TableCell className="border border-black p-2 text-center">
                  {vaccine.remarks}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>

      {/* Footer Instructions */}
      <p className="mt-6 text-sm">
        <strong>Instructions:</strong> Sa column ng{" "}
        <strong>Petsa ng Bakuna</strong>, isulat ang petsa ng pagbibigay ng
        bakuna. Sa column ng <strong>Remarks</strong>, isulat ang petsa ng
        pagbalik para sa susunod na dose, o anumang mahalagang impormasyon na
        maaaring makaapekto sa pagbabakuna ng bata.
      </p>
    </div>
  );
};

